	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Flip the little Dolphin","date":"10 Jan 2004","slug":"/2004-01-10-flip-the-little-dolphin/","tags":"Y2004"},"html":"<p>Yesterday Christopher picked out a <a title=\"Amazon.de: Bücher: Flip, der kleine Delphin\" href=\"http://www.amazon.de/exec/obidos/ASIN/3551086516/qid=1073768033\">picture book</a> from the <a href=\"http://www.carlsenkids.de/programm/lesemaus/\">Carlsen 'Lesemaus' series</a>. It is  about a baby dolphin, including pictures of the dolphin being born and nursing. OK, we can handle questions about that. The birth isn't so interesting for Christopher yet, but he noticed the nursing. 'The baby dolphin is drinking, like I used to drink from Mama!' Is his memory really that good? Not really... when I read to him this evening, he said he used to drink from Papa. Mama could only laugh.</p>"},"next":{"fields":{"title":"Practice Fee","date":"09 Jan 2004","slug":"/2004-01-09-practice-fee/"}},"previous":{"fields":{"title":"Two Polls","date":"10 Jan 2004","slug":"/2004-01-10-two-polls/"}}},{"node":{"fields":{"title":"Practice Fee","date":"09 Jan 2004","slug":"/2004-01-09-practice-fee/","tags":"Y2004"},"html":"<p>All week long we've been hearing stories about the chaos caused by the new 'practice fee' <em>(Praxisgebühr)</em> for health care introduced at the beginning of the year. Applied to those with public health insurance (90% of all Germans), 10 Euros is to be paid for each doctor visited per quarter in cash at the time of the first visit. The fee is waived for children and for referrals. The idea is to discourage patients from going directly to specialists without a referral from their family physician. Although the fee is collected by physicians, it goes entirely to the insurance funds. The physicians bear the administrative costs and the risk for those patients they treat but who refuse to pay.</p>\n<p>But the devil is in the details. Germans in the public insurance system have never seen a bill for their health care... now they suddenly have to pay before they see a doctor. The fee also applies to emergency rooms and ambulances (unless you called the ambulance through 110), so emergency personnel need to carry cash boxes and issue receipts. (The fee also applies to those given care involuntarily... imagine trying to collect cash from a drunk being admitted to detox.) The fee applies to welfare recipients and pensioners. There is a cap of 2% of annual income (1% for the 'chronically ill', which the law doesn't really define) for all practice fees and co-payments... so make sure you save all your receipts.</p>\n<p>The fee was part of the bi-partisan health reform law of last year, so both major parties are to blame. Best of all, the members of Bundestag <a title=\"Praxisgebühren-Rabatt: Die Parlamentarier haben verstanden - Wirtschaft - SPIEGEL ONLINE\" href=\"http://www.spiegel.de/wirtschaft/0,1518,281184,00.html\">exempted themselves from the fee</a>, along with civil servents. Instead of 10 Euros per doctor per quarter, they pay a flat 20 Euros per year.</p>\n<p>My guess is the practice fee will quickly be repealed.</p>"},"next":{"fields":{"title":"ARD Tagesschau: News in English","date":"07 Jan 2004","slug":"/2004-01-07-ard-tagesschau-news-in-english/"}},"previous":{"fields":{"title":"Flip the little Dolphin","date":"10 Jan 2004","slug":"/2004-01-10-flip-the-little-dolphin/"}}},{"node":{"fields":{"title":"ARD Tagesschau: News in English","date":"07 Jan 2004","slug":"/2004-01-07-ard-tagesschau-news-in-english/","tags":"Y2004"},"html":"<p><a title=\"Actually, it's by Deutsche Welle\" href=\"http://www.tagesschau.de/english/\">ARD Tagesschau: News in English</a></p>"},"next":{"fields":{"title":"Otitis Media","date":"06 Jan 2004","slug":"/2004-01-06-otitis-media/"}},"previous":{"fields":{"title":"Practice Fee","date":"09 Jan 2004","slug":"/2004-01-09-practice-fee/"}}}],"pathPrefix":"","first":false,"last":false,"index":557,"pageCount":1101}}

